import { Button } from '@ya.praktikum/react-developer-burger-ui-components';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './page404.module.css';
import { FC } from 'react';


const Page404: FC = () => {
  const navigate = useNavigate();

  return (
    <div className={styles.block}>
      <p className='text text_type_main-medium'>!</p>
      <p className='text text_type_digits-large'>404</p>
      <h3 className='text text_type_main-medium'>  !</h3>
      <div className={styles.links}>
        <Button htmlType="button" type="secondary" size="medium" onClick={() => navigate('/')}> </Button>
        <Button htmlType="button" type="secondary" size="medium" onClick={() => navigate('/login')}></Button>
      </div>
    </div>
  );
}

export default Page404;
