import {
  WS_CONNECTION_SUCCESS,
  WS_CONNECTION_ERROR,
  WS_CONNECTION_CLOSED,
  WS_GET_ORDERS,
} from '../actions/ws-actions';
import { TWsActions } from '../actions/ws-actions';
import { TOrder } from '../../types/types';

type TinitialState = {
  wsConnected: boolean,
  wsError: string | undefined,
  orders: Array<TOrder> | null,
  total: number,
  totalToday: number
}

const initialState: TinitialState = {
  wsConnected: false,
  wsError: undefined,
  orders: null,
  total: 0,
  totalToday: 0
}

//    WebSocket
export const wsReducer = (state = initialState, action: TWsActions) => {
  switch (action.type) {
    //      WS_CONNECTION_SUCCESS
    //   wsConnected   true
    case WS_CONNECTION_SUCCESS:
      return {
        ...state,
        wsError: undefined,
        wsConnected: true
      };

    //      WS_CONNECTION_ERROR
    //   wsConnected   false     action.payload
    case WS_CONNECTION_ERROR:
      return {
        ...state,
        wsError: action.payload,
        wsConnected: false
      };

    //      WS_CONNECTION_CLOSED,   
    //   wsConnected   false
    case WS_CONNECTION_CLOSED:
      return {
        ...state,
        wsError: undefined,
        wsConnected: false,
        orders: null,
      };

    //      WS_GET_ORDERS
    //  ,     
    case WS_GET_ORDERS:
      return {
        ...state,
        wsError: undefined,
        orders: action.payload.orders,
        total: action.payload.total,
        totalToday: action.payload.totalToday
      };
    default:
      return state;
  }
};

