import React from 'react';
import styles from './OrderDetails.module.css'
import confirmImg from '../../images/confirm.svg';
import { useSelector } from '../../types/hooks';
import { FC } from 'react';

const OrderDetails: FC = () => {
  const zeroLength = 6;
  const orderInfo = useSelector((store) => store.orderInformation.orderNumber); // 
  const orderNumber = String(orderInfo).padStart(zeroLength, '0');

  return (
    <div className={`${styles.orderDetales}`}>
      {orderInfo === null
        ? <p className='text text_type_main-large pb-8'></p>
        : <p className='text text_type_digits-large pb-8'>{orderNumber}</p>
      }
      <h3 className='text text_type_main-medium'> </h3>
      <img src={confirmImg} alt='  ' className='pt-15 pb-15' />
      <p className='text text_type_main-default pb-2'>   </p>
      <p className='text text_type_main-default order-detales-text-color'>    </p>
    </div>
  );
}

export default OrderDetails;
